<template>
  <div class="user-management">
    <el-card class="filters" shadow="never">
      <div slot="header" class="card-header">
        <div><span class="emoji-icon">üë•</span> Áî®Êà∑ÁÆ°ÁêÜ</div>
      </div>
      
      <!-- Á≠õÈÄâË°®Âçï -->
      <el-form :inline="true" :model="filters" size="mini">
        <el-form-item label="ÊøÄÊ¥ªÁä∂ÊÄÅ">
          <el-select v-model="filters.is_active" placeholder="ÂÖ®ÈÉ®" clearable style="width: 120px">
            <el-option label="Â∑≤ÊøÄÊ¥ª" value="true"></el-option>
            <el-option label="Êú™ÊøÄÊ¥ª" value="false"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="fetchUsers">Êü•ËØ¢</el-button>
          <el-button @click="handleReset">ÈáçÁΩÆ</el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <!-- Áî®Êà∑ÂàóË°®Ë°®Ê†º -->
    <el-card shadow="never" style="margin-top: 20px">
      <el-table
        v-loading="loading"
        :data="users"
        stripe
        style="width: 100%"
        border
      >
        <el-table-column prop="id" label="ID" width="80" align="center"></el-table-column>
        <el-table-column prop="username" label="ÊâãÊú∫Âè∑" width="130" align="center"></el-table-column>
        <el-table-column prop="email" label="ÈÇÆÁÆ±" width="180" show-overflow-tooltip></el-table-column>
        <el-table-column prop="is_active" label="ÊøÄÊ¥ªÁä∂ÊÄÅ" width="100" align="center">
          <template slot-scope="scope">
            <el-tag :type="scope.row.is_active ? 'success' : 'info'" size="mini">
              {{ scope.row.is_active ? 'Â∑≤ÊøÄÊ¥ª' : 'Êú™ÊøÄÊ¥ª' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_premium" label="È´òÁ∫ßÁî®Êà∑" width="100" align="center">
          <template slot-scope="scope">
            <el-tag :type="scope.row.is_premium ? 'warning' : ''" size="mini">
              {{ scope.row.is_premium ? 'ÊòØ' : 'Âê¶' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="fingerprint" label="Fingerprint" width="200" show-overflow-tooltip>
          <template slot-scope="scope">
            <span v-if="scope.row.fingerprint" style="font-family: monospace; font-size: 12px;">
              {{ scope.row.fingerprint.substring(0, 20) }}...
            </span>
            <span v-else style="color: #909399;">Êú™ÁªëÂÆö</span>
          </template>
        </el-table-column>
        <el-table-column prop="created_at" label="Ê≥®ÂÜåÊó∂Èó¥" width="180" align="center">
          <template slot-scope="scope">
            {{ formatDateTime(scope.row.created_at) }}
          </template>
        </el-table-column>
        <el-table-column prop="last_login_at" label="ÊúÄÂêéÁôªÂΩï" width="180" align="center">
          <template slot-scope="scope">
            {{ scope.row.last_login_at ? formatDateTime(scope.row.last_login_at) : '‰ªéÊú™ÁôªÂΩï' }}
          </template>
        </el-table-column>
        <el-table-column prop="token_expires_at" label="TokenËøáÊúü" width="180" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.token_expires_at">
              {{ formatDateTime(scope.row.token_expires_at) }}
            </span>
            <span v-else style="color: #909399;">Êó†</span>
          </template>
        </el-table-column>
        <el-table-column label="Êìç‰Ωú" width="280" fixed="right" align="center">
          <template slot-scope="scope">
            <el-button
              v-if="!scope.row.is_active"
              type="success"
              size="mini"
              @click="handleActivate(scope.row)"
              :loading="scope.row.activating"
            >
              ÊøÄÊ¥ª
            </el-button>
            <el-button
              v-else
              type="warning"
              size="mini"
              @click="handleDeactivate(scope.row)"
              :loading="scope.row.deactivating"
            >
              Á¶ÅÁî®
            </el-button>
            <el-button
              v-if="!scope.row.is_premium"
              type="primary"
              size="mini"
              @click="handleSetPremium(scope.row, true)"
              :loading="scope.row.settingPremium"
            >
              ËÆæ‰∏∫È´òÁ∫ß
            </el-button>
            <el-button
              v-else
              type="info"
              size="mini"
              @click="handleSetPremium(scope.row, false)"
              :loading="scope.row.settingPremium"
            >
              ÂèñÊ∂àÈ´òÁ∫ß
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- ÂàÜÈ°µ -->
      <el-pagination
        style="margin-top: 20px; text-align: right;"
        @size-change="handleSizeChange"
        @current-change="handlePageChange"
        :current-page="pagination.page"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="pagination.page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pagination.total"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'UserManagement',
  data() {
    return {
      loading: false,
      users: [],
      filters: {
        is_active: null
      },
      pagination: {
        page: 1,
        page_size: 20,
        total: 0,
        pages: 0
      }
    }
  },
  mounted() {
    this.fetchUsers()
  },
  methods: {
    /**
     * Ëé∑ÂèñÁî®Êà∑ÂàóË°®
     */
    async fetchUsers() {
      this.loading = true
      try {
        const params = {
          page: this.pagination.page,
          page_size: this.pagination.page_size
        }
        
        // Ê∑ªÂä†Á≠õÈÄâÊù°‰ª∂
        if (this.filters.is_active !== null && this.filters.is_active !== '') {
          params.is_active = String(this.filters.is_active)
        }
        
        const response = await this.$api.admin.getUserList(params)
        
        if (response.code === 200) {
          const data = response.data
          this.users = data.users || []
          this.pagination.total = data.total || 0
          this.pagination.pages = data.pages || 0
        } else {
          this.$message.error(response.message || 'Ëé∑ÂèñÁî®Êà∑ÂàóË°®Â§±Ë¥•')
        }
      } catch (error) {
        console.error('Ëé∑ÂèñÁî®Êà∑ÂàóË°®Â§±Ë¥•:', error)
        this.$message.error('Ëé∑ÂèñÁî®Êà∑ÂàóË°®Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËøûÊé•')
      } finally {
        this.loading = false
      }
    },

    /**
     * ÊøÄÊ¥ªÁî®Êà∑
     */
    async handleActivate(user) {
      this.$set(user, 'activating', true)
      try {
        const response = await this.$api.admin.activateUser(user.id)
        if (response.code === 200) {
          this.$message.success('Áî®Êà∑ÊøÄÊ¥ªÊàêÂäü')
          user.is_active = true
          this.fetchUsers()
        } else {
          this.$message.error(response.message || 'ÊøÄÊ¥ªÂ§±Ë¥•')
        }
      } catch (error) {
        console.error('ÊøÄÊ¥ªÁî®Êà∑Â§±Ë¥•:', error)
        this.$message.error('ÊøÄÊ¥ªÁî®Êà∑Â§±Ë¥•')
      } finally {
        this.$set(user, 'activating', false)
      }
    },

    /**
     * Á¶ÅÁî®Áî®Êà∑
     */
    async handleDeactivate(user) {
      this.$confirm('Á°ÆÂÆöË¶ÅÁ¶ÅÁî®ËØ•Áî®Êà∑ÂêóÔºüÁ¶ÅÁî®ÂêéÁî®Êà∑Â∞ÜÊó†Ê≥ïÁôªÂΩï„ÄÇ', 'ÊèêÁ§∫', {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning'
      }).then(async () => {
        this.$set(user, 'deactivating', true)
        try {
          const response = await this.$api.admin.deactivateUser(user.id)
          if (response.code === 200) {
            this.$message.success('Áî®Êà∑Â∑≤Á¶ÅÁî®')
            user.is_active = false
            this.fetchUsers()
          } else {
            this.$message.error(response.message || 'Á¶ÅÁî®Â§±Ë¥•')
          }
        } catch (error) {
          console.error('Á¶ÅÁî®Áî®Êà∑Â§±Ë¥•:', error)
          this.$message.error('Á¶ÅÁî®Áî®Êà∑Â§±Ë¥•')
        } finally {
          this.$set(user, 'deactivating', false)
        }
      }).catch(() => {})
    },

    /**
     * ËÆæÁΩÆ/ÂèñÊ∂àÈ´òÁ∫ßÁî®Êà∑
     */
    async handleSetPremium(user, isPremium) {
      this.$set(user, 'settingPremium', true)
      try {
        const response = await this.$api.admin.setPremium(user.id, isPremium)
        if (response.code === 200) {
          this.$message.success(response.message || (isPremium ? 'Â∑≤ËÆæÁΩÆ‰∏∫È´òÁ∫ßÁî®Êà∑' : 'Â∑≤ÂèñÊ∂àÈ´òÁ∫ßÁî®Êà∑'))
          user.is_premium = isPremium
          this.fetchUsers()
        } else {
          this.$message.error(response.message || 'Êìç‰ΩúÂ§±Ë¥•')
        }
      } catch (error) {
        console.error('ËÆæÁΩÆÈ´òÁ∫ßÁî®Êà∑Â§±Ë¥•:', error)
        this.$message.error('Êìç‰ΩúÂ§±Ë¥•')
      } finally {
        this.$set(user, 'settingPremium', false)
      }
    },

    /**
     * ÈáçÁΩÆÁ≠õÈÄâÊù°‰ª∂
     */
    handleReset() {
      this.filters = {
        is_active: null
      }
      this.pagination.page = 1
      this.fetchUsers()
    },

    /**
     * ÂàÜÈ°µÂ§ßÂ∞èÊîπÂèò
     */
    handleSizeChange(size) {
      this.pagination.page_size = size
      this.pagination.page = 1
      this.fetchUsers()
    },

    /**
     * È°µÁ†ÅÊîπÂèò
     */
    handlePageChange(page) {
      this.pagination.page = page
      this.fetchUsers()
    },

    /**
     * Ê†ºÂºèÂåñÊó•ÊúüÊó∂Èó¥
     */
    formatDateTime(dateTimeStr) {
      if (!dateTimeStr) return '-'
      try {
        const date = new Date(dateTimeStr)
        const year = date.getFullYear()
        const month = String(date.getMonth() + 1).padStart(2, '0')
        const day = String(date.getDate()).padStart(2, '0')
        const hours = String(date.getHours()).padStart(2, '0')
        const minutes = String(date.getMinutes()).padStart(2, '0')
        const seconds = String(date.getSeconds()).padStart(2, '0')
        return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`
      } catch (error) {
        return dateTimeStr
      }
    }
  }
}
</script>

<style scoped>
.user-management {
  padding: 20px;
}

